.newsletter {
  composes: calderLC from '../../shared/css/fonts.css';
  max-width: 210px;
  padding: 55px 0 100px;
}
  .newsletter img {
    display: block;
    height: auto;
    margin: 0 auto 26px;
    max-width: 100%;
    width: 168px;
  }
  .newsletter .visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  .newsletter input[type=email], .newsletter input[type=text], .newsletter textarea {
    font-size: 13px;
    font-family: "calder-lc", sans-serif;
    background-color: #10363f;
    border: 1px solid #f2ece6;
    border-radius: 2px;
    box-sizing: border-box;
    color: #f2ece6;
    padding: 17px 15px;
    margin: 0 0 16px;
    width: 100%;
  }
  .newsletter input[type=email]::placeholder, .newsletter input[type=text]::placeholder, .newsletter textarea::placeholder {
      color: #f2ece6;
    }
  .newsletter input[type=email]:focus, .newsletter input[type=text]:focus, .newsletter textarea:focus {
      background-color: #10363f;
      outline: none;
    }
  .newsletter .submit {
    margin: 8px 0 0;
    text-align: center;
  }
  .newsletter .submit input {
      background-color: #ef5a47;
      border: 1px solid #ef5a47;
      border-radius: 20px;
      cursor: pointer;
      display: inline-block;
      color: #f2ece6;
      font-family: "calder-dark", sans-serif;
      padding: 5px 27px 4px;
      text-align: center;
      text-decoration: none;
      text-transform: uppercase;
      transition: all 0.1s ease-out;
      -webkit-font-smoothing: antialiased;
      -moz-font-smoothing: antialiased;
      -o-font-smoothing: antialiased;
    }
  .newsletter .submit input:active,
      .newsletter .submit input:focus,
      .newsletter .submit input:hover {
        background: none;
        color: #ef5a47;
        outline: none;
      }
