.title {
  font-size: 30px;
  line-height: 40px;
  margin: 26px 20px 30px;
}
  @media all and (min-width: 25em) and (max-width: 41.6875em) {.title {
    margin-top: 50px
}
  }
  @media all and (min-width: 41.75em) {.title {
    margin: 50px 40px 8px;
    max-width: calc(75% - 100px)
}
  }
.contentWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.contentWrapper img {
    max-width: 100%;
  }
.contentArea {
  flex: 1 0 auto;
  margin: 0 20px;
}
@media all and (min-width: 41.75em) {
.contentArea {
    display: flex;
    justify-content: space-between;
    margin: 0 40px
}
  }
.field {
  border-top: 1px dashed #b2aeaa;
  min-width: calc(50% - 10px);
  padding: 7px 0 12px;
}
@media all and (min-width: 25em) and (max-width: 41.6875em) {
.field {
    width: calc(50% - 10px)
}
  }
.field a {
    box-shadow: inset 0 -1px 0 #f0c26d;
    color: #10363f;
    text-decoration: none;
    transition: background .3s ease-out;
  }
.field a:hover {
      background: #f0c26d;
    }
.button {
  background: #ef5a47;
  border: 1px solid #ef5a47;
  border-radius: 20px;
  box-shadow: none !important;
  color: #f2ece6;
  composes: calderDark from './fonts.css';
  font-size: 11px;
  letter-spacing: 1px;
  line-height: 100%;
  margin-top: 20px;
  outline: none;
  padding: 6px 18px;
  text-transform: uppercase;
  transition: all .3s ease-out;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
}
.button:active,
  .button:focus,
  .button:hover {
    background: none;
    color: #ef5a47;
    cursor: pointer;
  }
