.footer {
  background: url('/images/bg_footer.png') top center no-repeat;
  background-size: cover;
  height: 528px;
  margin-top: 100px;
  text-align: center;
}
.logo {
  margin-top: 56px;
  width: 98px;
}
.info {
  color: #b2aeaa;
  margin: 20px 0 40px;
}
.info .social img {
      height: 14px;
      margin-top: 4px;
      width: auto;
    }
.info .social + .separator {
      display: inline;
    }
.info .social a:hover {
      border-bottom: 1px solid;
      padding-top: 8px;
    }
.heading {
  width: 140px;
}
.sponsorsContainer {
  display: flex;
  justify-content: center;
}
.sponsorsContainer img {
    filter: grayscale(100%) brightness(240%);
    -webkit-filter: grayscale(100%) brightness(240%);
    margin: 20px 10px;
    transform: scale(1.65);
    width: 100px;
  }
.credits {
  composes: calderDark from '../../shared/css/fonts.css';
  color: #35647e;
  font-size: 10px;
  padding: 20px;
  text-align: center;
}
.credits a {
    color: #35647e;
  }
