.newOverview.teaserWrapper {
    border-bottom: 1px dashed #b2aeaa;
    padding: 25px 0 33px;
  }
    .newOverview.teaserWrapper:first-child {
      padding-top: 0;
    }
.date {
  border-bottom: 1px dashed #b2aeaa;
  color: #b2aeaa;
  display: initial;
  font-size: 11px;
}
.moreLink {
  background: url('/images/plus.svg') left 5px no-repeat;
  background-size: 8px;
  font-size: 11px;
  margin-top: 20px;
  padding-left: 15px;
  text-transform: uppercase;
}
.hompageNews {
  padding: 45px 20px 55px;
}
@media all and (min-width: 41.75em) {
.hompageNews {
    padding-left: 40px;
    padding-right: 40px
}
    .hompageNews:last-child {
      flex: 1 1 auto;
    }
  }
.hompageNews:nth-child(3n+1) {
    background: #f7f1eb;
  }
.hompageNews:nth-child(3n+3) {
    background: #ede7e1;
  }
.hompageNews.secondColumn:nth-child(3n+1) {
      background: #ede7e1;
    }
.hompageNews.secondColumn:nth-child(3n+2) {
      background: #f7f1eb;
    }
.hompageNews.secondColumn:nth-child(3n+3) {
      background: #f2ece6;
    }
