
/* Menu Toggle Button */
.menuButton {
  composes: calderDark from '../../shared/css/fonts.css';
  align-items: center;
  background-color: #10363f;
  border: 0 none;
  color: #f2ece6;
  cursor: pointer;
  display: flex;
  font-size: 20px;
  height: 56px;
  line-height: 18px;
  padding: 0;
  position: fixed;
  right: 0;
  user-select: none;
  text-transform: uppercase;
  top: 0;
  width: 56px;
  word-wrap: break-word;
  z-index: 1000;
}
.menuButton:focus {
    outline: none;
  }

/* Menu Button Diagonal Line Animation */
.menuButton span {
    content: "";
    left: 0;
    position: absolute;
    transform: rotate(0deg);
    transform-origin: 0% 0%;
    transition: transform 0.2s linear;
    width: 80px;
  }
.menuButton .diagonalTopLine {
    bottom: -2px;
    border-top: 2px solid #f2ece6;
    z-index: 1;
  }
.menuButton .diagonalBottomLine {
    bottom: -5px;
    border-top: 3px solid #10363f;
  }
.menuButton.active {
  overflow: hidden;
}
.menuButton.active span {
    transform: rotate(-45deg);
  }

/* Menu Button Horizontal Line Animation */
.menuButton.inactive span {
    border-color: transparent;
  }
.menuButton.inactive:before,
  .menuButton.inactive:after {
    border-top: 2px solid #10363f;
    content: "";
    display: block;
    position: absolute;
    transition: all 0.1s linear;
    width: 100%;
  }
.menuButton.inactive:before {
    bottom: -4px;
  }
.menuButton.inactive:after {
    bottom: -8px;
  }
.menuButton.inactive:hover:before {
      bottom: -7px;
    }
.menuButton.inactive:hover:after {
      bottom: -14px;
    }

/* Print the word "MENU" as an image for cross-browser consistency */
.menuButtonImage {
  display: block;
  margin: 0 auto;
  width: 32px;
}

/* Expanding / Collapsing Overlay */
.menuOverlay {
  background-color: #10363f;
  color: #f2ece6;
  position: fixed;
  right: 0;
  text-align: left;
  top: 0;
  z-index: 999;
}
.menuOverlay.inactive {
  height: 56px;
  overflow: hidden;
  transition: width 0.2s linear, height 0.2s linear 0.2s;
  width: 56px;
}
.menuOverlay.inactive nav {
    opacity: 0;
  }
.menuOverlay.active {
  height: 100vh;
  overflow-y: auto;
  transition: height 0.2s linear, width 0.2s linear 0.2s;
  width: 100vw;
}
@media screen and (min-width: 400px) and (max-width: 699px) {
.menuOverlay.active {
    max-width: 400px
}
  }
@media screen and (min-width: 700px) and (max-width: 959px) {
.menuOverlay.active {
    max-width: 700px
}
  }
@media all and (min-width: 60em) and (max-width: 74.9375em) {
.menuOverlay.active {
    width: 65%
}
  }
@media all and (min-width: 75em) and (max-width: 87.4375em) {
.menuOverlay.active {
    width: 60%
}
  }
@media all and (min-width: 87.5em) {
.menuOverlay.active {
    width: 50vw
}
  }
.menuOverlayContainer {
  box-sizing: border-box;
  height: 100%;
  justify-content: center;
  padding: 0 56px;
  width: 100%;
}
@media screen and (min-width: 700px) {
.menuOverlayContainer {
    display: flex
}
  }

/* Menu Items */
@media screen and (min-width: 700px) {
.siteNavigation {
    max-width: 210px;
    width: 50%
}
  }
.menu {
  list-style: none;
  margin: 0;
  padding: 100px 0 55px;
}
.menuItem {
  composes: calderDark from '../../shared/css/fonts.css';
  font-size: 1.875em;
  font-size: 30px;
  line-height: 30px;
  margin: 0 0 20px;
}
.menuItemSmall {
  composes: calderDark from '../../shared/css/fonts.css';
  font-size: 12px;
  line-height: 12px;
  margin: 12px 0 0;
}
.menuItem,
.menuItemSmall {
  background: none;
  display: inline-block;
  padding: 0;
  width: 100%;
}
.menuItem a, .menuItemSmall a {
    box-shadow: none;
    color: #f2ece6;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transition: color 0.1s ease;
  }
.menuItem a:hover, .menuItemSmall a:hover {
      background: none;
      color: #ef5a47;
    }
/* Every time a nav menu gets added, we need to add another nth-child selector.
     Otherwise, the new menu item will be hidden */
.menuOverlay.active .menuItem, .menuOverlay.active .menuItemSmall {
    opacity: 0;
  }
.menuOverlay.active .menuItem:nth-child(1), .menuOverlay.active .menuItemSmall:nth-child(1) {
      animation: fadeInMenuItem 0.1s 0.4s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(2), .menuOverlay.active .menuItemSmall:nth-child(2) {
      animation: fadeInMenuItem 0.1s 0.5s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(3), .menuOverlay.active .menuItemSmall:nth-child(3) {
      animation: fadeInMenuItem 0.1s 0.6s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(4), .menuOverlay.active .menuItemSmall:nth-child(4) {
      animation: fadeInMenuItem 0.1s 0.7s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(5), .menuOverlay.active .menuItemSmall:nth-child(5) {
      animation: fadeInMenuItem 0.1s 0.8s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(6), .menuOverlay.active .menuItemSmall:nth-child(6) {
      animation: fadeInMenuItem 0.1s 0.9s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(7), .menuOverlay.active .menuItemSmall:nth-child(7) {
      animation: fadeInMenuItem 0.1s 1s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(8), .menuOverlay.active .menuItemSmall:nth-child(8) {
      animation: fadeInMenuItem 0.1s 1.1s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(9), .menuOverlay.active .menuItemSmall:nth-child(9) {
      animation: fadeInMenuItem 0.1s 1.2s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(10), .menuOverlay.active .menuItemSmall:nth-child(10) {
      animation: fadeInMenuItem 0.1s 1.3s ease-in both;
    }
.menuOverlay.active .menuItem:nth-child(11), .menuOverlay.active .menuItemSmall:nth-child(11) {
      animation: fadeInMenuItem 0.1s 1.4s ease-in both;
    }

/* Menu Content Animations */
@keyframes fadeInMenuItem {
  from {
    opacity: 0;
    transform: translate3d(0, -10%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes growLine {
  from {
    transform: translate3d(0, -100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fadeInForm {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Newsletter Form */
.newsletterContainer {
  display: none;
}
@media screen and (min-width: 700px) {
.newsletterContainer {
    display: flex;
    flex-direction: column;
    min-width: 210px;
    width: 50%
}
  }
@media screen and (min-width: 700px) {
.newsletterLine {
    flex: 1;
    max-height: 700px;
    position: relative
}
    .newsletterLine:after {
      background-color: #ef5a47;
      border-radius: 0 0 2px 2px;
      content: '';
      height: 100%;
      left: 104px;
      position: absolute;
      width: 2px;
    }
  }
.menuOverlay.inactive .newsletterLine {
    opacity: 0;
  }
.menuOverlay.active .newsletterLine {
    animation: growLine 0.7s 0.4s ease-in both;
  }
.menuOverlay.inactive .newsletterForm {
    display: none;
    opacity: 0;
  }
.menuOverlay.active .newsletterForm {
    animation: fadeInForm 0.2s 1.2s ease-in both;
    display: block;
  }
